import React,{Fragment} from 'react'
import {Button,Modal} from 'antd'
import './index.css'
import PubSub from 'pubsub-js'
import {Link} from 'react-router-dom'
import navBtn from '../../common/img/navBtn.png'
import Carousel from '../../components/carousel'
import img1 from '../../common/img/carousel/1.png'
import img2 from "../../common/img/carousel/2.png"
import img3 from "../../common/img/carousel/3.png"
import img4 from "../../common/img/carousel/4.png"
import img5 from "../../common/img/carousel/5.png"
import img6 from "../../common/img/carousel/6.png"
export default function Header() {
  let [HasLogin,SetHasLogin]=React.useState(false)
  let [UserName,SetUserName]=React.useState('')
  const [isModalVisible, setIsModalVisible] = React.useState(false);
  let [hasNav,setHasNav]=React.useState(false)
  const HandleHasLogin=(mas,data)=>{
    SetHasLogin(data)
  }
  const handleOk=()=>{
    setIsModalVisible(false)
  }
  const handleCancel=()=>{
    setIsModalVisible(false)
  }
  const HandleUserName=(mas,data)=>{
    SetUserName(data)
  }
  const offLogin=()=>{
    PubSub.publish('HasLogin',false)
  }
  const showModal=()=>{
    setIsModalVisible(true)
  }
  const HandleShowModal=(title,data)=>{
    setIsModalVisible(true)
  }
  let ModalStyle={}
  let ModalWidth=''
  if(document.body.clientWidth>500){
    ModalWidth='60vw'
    ModalStyle={height: '30vw',backgroundColor:'rgba(240, 123, 63)'}
  }else{
    ModalWidth='80vw'
    ModalStyle={height: '50vh',backgroundColor:'rgba(240, 123, 63)'}
  }
  const img=[img1,img2,img3,img4,img5,img6]
  const clickNav=()=>{
    // // 字符串0.5
    // document.getElementsByTagName('body')[0].style.opacity='0.6'
    if(!hasNav){
      PubSub.publish('navHeight','45vh')
    }else{
      PubSub.publish('navHeight','0')
    }
    setHasNav(!hasNav)
  }
  React.useEffect(()=>{
    PubSub.subscribe('ShowModal',HandleShowModal)
    PubSub.subscribe('HasLogin',HandleHasLogin)
    PubSub.subscribe('UserName',HandleUserName)
  })
    return (
      <Fragment>
        {/* 移动端占位空间 */}
      <div className='header-space'></div>
        <div className="header">
        {/* 只是为了让页面加载antd的默认样式 */}
        <Button type="primary" className='header-antdDefault'>Button</Button>  
      <span className="header-title">Aays's blog</span>
<Modal title="Basic Modal" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel} width={ModalWidth} bodyStyle={ModalStyle} centered>
           <div className='header-carousel'>
        <Carousel img={img}/>
        </div>
        <div className='header-carousel-text'>
          WeChat：Escaay
         <hr></hr>
          CSDN：Aays2790
          <hr></hr>
          Github：Escaay
          <hr></hr>
          </div>
      </Modal>
      <div className='header-navBtn' onClick={clickNav}><img src={navBtn} className='header-navBtnImg'/></div>
        {
          HasLogin?
          (<div className='header-user'><div className='header-username'>Hello {UserName}<span><Button type="primary" style={{height:'5vh',backgroundColor:'red',borderRadius:'25px',border:'1px solid red',marginLeft:'1vw',position:'relative',bottom:'0.6vh'}} onClick={offLogin}>
          退出登录
          </Button>  </span></div></div>)
          : 
          (<div className='header-user'>
                 <Button type="primary" onClick={showModal} style={{width:'10vw',fontFamily:'Sans-serif'}}>
        Contact me
      </Button>
            <Link to='/user/login'>
          <Button type="primary" style={{height:'5vh',backgroundColor:'rgba(0,0,0,0)',border:'none'}}>Sign in</Button>  
         </Link>
         <Link to='/user/register'> 
         <Button type="primary" style={{height:'5vh',backgroundColor:'rgba(0,0,0,0)',borderRadius:'25px',border:'1px solid white'}}>
           Sign up
           </Button>  
          </Link> </div>)
        }
  </div>
  </Fragment>
    )
}
