import React from 'react'
import './index.css'
export default function Loading() {
   const [rotate,setRotate]= React.useState('20')
   React.useEffect(()=>{
const timer=setInterval(()=>{
    setRotate(rotate=>String(Number(rotate)+4))
    // useState异步，所以都是初始值，可以在setRotate里面输出
    // console.log(rotate);
},10)
// 无法执行,Suspense fallback无法检测到组件卸载的状况，无法清除定时器
return ()=>{
    clearInterval(timer)
}
   },[])
    return (
        <div className='loading'>
            <div className='loading-box' style={{transform:`rotate(${rotate}deg)`}}></div>
        <div className='loading-text'>
            <h1 >Loading...</h1>
            </div>
        </div>
    )
}
