import {Switch,Route} from 'react-router-dom'
import React,{lazy,Suspense} from 'react'
import {Fragment} from 'react'
import {Redirect} from 'react-router-dom'
import './App.css'
import './common/font/iconfont.css'
import Header from './components/header'
import Nav from './components/nav'
import Loading from '../src/components/loading'
import Footer from '../src/components/footer'
const User=lazy(()=>import('../src/pages/user'))
const Home=lazy(()=>import('../src/pages/home'))
const Essay=lazy(()=>import('../src/pages/essay'))
const Tool=lazy(()=>import('./pages/tool'))
const Tecnology=lazy(()=>import('../src/pages/tecnology'))
const Message=lazy(()=>import('../src/pages/message'))
function App() {
  return (
    <Fragment>
   <Header />
   <Nav />
   <Footer />
<Suspense fallback={<Loading />}>
    <Switch>
     <Route exact path='/home' component={Home}/>
     <Route path='/essay' component={Essay}/>
     <Route path='/tool' component={Tool}/>
     <Route path='/tecnology' component={Tecnology}/>
     <Route path='/message' component={Message}/>
     <Route path='/user' component={User}/>
     <Redirect to='/home'></Redirect>
    </Switch> 
    </Suspense>
    </Fragment>
  );
}

export default App;
