import React,{Fragment} from 'react'
import './index.css'
import {NavLink} from 'react-router-dom'
import {Link} from 'react-router-dom'
import PubSub from 'pubsub-js'
import {Button} from 'antd'
export default function Nav() {
  const offLogin=()=>{
    PubSub.publish('HasLogin',false)
  }
  const showModal=()=>{
    PubSub.publish('ShowModal',true)
  }
    React.useEffect(()=>{
        PubSub.subscribe('HasLogin',HandleHasLogin)
        PubSub.subscribe('UserName',HandleUserName)
        PubSub.subscribe('navHeight',handleNavHeight)
        PubSub.publish('HasLogin',false)
        return ()=>{
          PubSub.unsubscribe('HasLogin',HandleHasLogin)
          PubSub.unsubscribe('UserName',HandleUserName)
          PubSub.unsubscribe('navHeight',handleNavHeight)
        }
      },[])
    let [HasLogin,SetHasLogin]=React.useState(false)
    let [UserName,SetUserName]=React.useState('')
    const HandleHasLogin=(mas,data)=>{
      SetHasLogin(data)
    }
    const HandleUserName=(mas,data)=>{
      SetUserName(data)
    }
    let [navHeight,setNavHeight]=React.useState('0')
    const handleNavHeight=(title,data)=>{
        setNavHeight(data)
    }
    const clickNav=()=>{
      PubSub.publish('navHeight','0')
    }
    return (
       <Fragment>
        <div className='nav-pc'>
        <ul>
            <li> <NavLink to='/home' activeClassName='navActive'><span className="icon-gaishu iconfont nav-pc-icon"></span>首页</NavLink></li>
            <li> <NavLink to='/essay' activeClassName='navActive'><span className="icon-tubiaozhizuomoban- iconfont nav-pc-icon"></span>项目展示</NavLink></li>
            <li> <NavLink to='/tecnology' activeClassName='navActive'><span className="icon-gouwuche iconfont nav-pc-icon"></span>技术博客</NavLink></li>
            <li> <NavLink to='/tool' activeClassName='navActive'><span className="icon-gerenzhongxin iconfont nav-pc-icon"></span>功能栏</NavLink></li>
            <li> <NavLink to={{pathname:'/message',state:{HasLogin,username:UserName}}} activeClassName='navActive'><span className="icon-guanyuwomen iconfont nav-pc-icon"></span>留言板</NavLink></li>
        </ul>
    </div>
    <div className='nav-pe' style={{height:navHeight}}>
    {
          HasLogin?
          (<div className='nav-user'><div className='nav-username'>Hello {UserName}<span className='nav-username-btn'><Button type="primary" style={{height:'4vh',backgroundColor:'red',borderRadius:'25px',border:'1px solid red',marginLeft:'1vw',position:'relative',bottom:'-1vh'}} onClick={offLogin}>
          退出登录
          </Button>  </span></div></div>)
          : 
          (<div className='nav-user' onClick={clickNav}>
            <Link to='/user/login'>
          <Button type="primary" style={{height:'5vh',backgroundColor:'rgba(0,0,0,0)',border:'none'}}>Sign in</Button>  
         </Link>
         <Link to='/user/register'> 
         <Button type="primary" style={{height:'5vh',backgroundColor:'rgba(0,0,0,0)',borderRadius:'25px',border:'1px solid white'}}>
           Sign up
           </Button>  
          </Link> 
          <Button type="primary" onClick={showModal} style={{width:'28vw',marginRight:'0.5vw',fontFamily:'Sans-serif'}}>
        Contact me
      </Button>
          </div>)
        }
        <ul onClick={clickNav}>
            <li> <NavLink to='/home' activeClassName='navActive'><span className="icon-gaishu iconfont nav-pe-icon"></span> 首页</NavLink></li>
            <li> <NavLink to='/essay' activeClassName='navActive'><span className="icon-tubiaozhizuomoban- iconfont nav-pe-icon"></span> 项目展示</NavLink></li>
            <li> <NavLink to='/tecnology' activeClassName='navActive'><span className="icon-gouwuche iconfont nav-pe-icon"></span> 技术博客</NavLink></li>
            <li> <NavLink to='/tool' activeClassName='navActive'><span className="icon-gerenzhongxin iconfont nav-pe-icon"></span> 功能栏</NavLink></li>
            <li> <NavLink to={{pathname:'/message',state:{HasLogin,username:UserName}}} activeClassName='navActive'><span className="icon-guanyuwomen iconfont nav-pe-icon"></span> 留言板</NavLink></li>
        </ul>
    </div>
    </Fragment>
    )
}
